.App {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: center;
    justify-content: center;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;    
    color: #FFF;
}
.App.online {
  background-color: #8BC34A;
}
.App.offline {
  background-color: #F44336;
}

.App .App-status {
  font-size: 32px;
}


